div[slot=breadcrumb] {
  position: relative;
  z-index: 10;
}

.fdcp-filtered-search h2 {
  margin-bottom: 2rem;
}
.fdcp-filtered-search .fdcp-search-box-wrapper {
  position: relative;
  max-width: 100%;
  margin-bottom: 1rem;
}
.fdcp-filtered-search .fdcp-search-box-wrapper input[type=search] {
  position: relative;
  padding: 16px 32px 16px 60px;
  width: 100%;
  border-radius: 40px;
  border: 1px solid #A8ADB4;
  background: #FFF url("/_content/Foundation.Components/images/Icons/MagnifyingGlass.svg") no-repeat;
  background-position: 32px 50%;
  background-size: 1.5rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, outline 0.15s ease-in-out;
  font-size: 1.25rem;
  color: #333;
}
.fdcp-filtered-search .fdcp-search-box-wrapper input[type=search]:focus {
  outline: var(--gcds-input-outline-width) solid var(--gcds-input-focus-border);
  outline-offset: var(--gcds-input-border-width);
}

.filter-panel {
  border: 1px solid #D6D9DD;
  border-radius: 5px;
  padding: 20px 16px;
}
.filter-panel h3 {
  font-family: var(--gcds-font-families-heading);
  font-style: normal;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.filter-panel .filter-section {
  margin-bottom: 1.5rem;
}
.filter-panel .filter-section .fdcp-collapse-button {
  border: none;
  background: transparent;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  align-self: stretch;
  width: 100%;
  color: #000;
  font-family: var(--gcds-font-families-body);
  font-size: 17.778px;
  font-style: normal;
  font-weight: 500;
  position: relative;
  margin-bottom: 16px;
  padding: 0;
}
.filter-panel .filter-section .fdcp-collapse-button:after {
  content: "\f077";
  position: absolute;
  right: 0;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
}
.filter-panel .filter-section .fdcp-collapse-button[aria-expanded=true]::after {
  content: "\f078";
}
.filter-panel .filter-section .fdcp-collapse {
  display: none;
  transition: all 0.3s ease;
}
.filter-panel .filter-section .fdcp-collapse.fdcp-show {
  display: block;
}
.filter-panel .filter-section .filter-option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  padding-left: 0.2rem;
}
.filter-panel .filter-section .filter-option input[type=checkbox] {
  display: none;
}
.filter-panel .filter-section .filter-option input[type=checkbox] + label {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  padding-left: 1.75rem;
  font-size: 1rem;
  color: #000;
}
.filter-panel .filter-section .filter-option input[type=checkbox] + label::before {
  content: "";
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 2px solid #333;
  border-radius: 0.2rem;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: border-color 0.15s ease-in-out, background-image 0.15s ease-in-out, outline 0.15s ease-in-out;
}
.filter-panel .filter-section .filter-option input[type=checkbox]:checked + label::before {
  background-color: transparent;
  border-color: #1d4ed8;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5l3 3 7-7' stroke='%231d4ed8' stroke-width='2' fill='none'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}
.filter-panel .filter-section .filter-option .filter-count {
  background-color: #D7E5F5;
  color: #000;
  padding: 0.2rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 600;
  min-width: 1.5rem;
  text-align: center;
  width: 2rem;
}

.fdcp-badge {
  position: relative;
  display: inline-flex;
  padding: 0.25rem 0.75rem;
  border-radius: 1.875rem;
  white-space: nowrap;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 500;
  vertical-align: middle;
  gap: 0.25rem;
}
.fdcp-badge .fdcp-badge-start,
.fdcp-badge .fdcp-badge-end {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.fdcp-badge .fdcp-badge-start > *,
.fdcp-badge .fdcp-badge-end > * {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: inherit;
  line-height: inherit;
}
.fdcp-badge .fdcp-badge-start i, .fdcp-badge .fdcp-badge-start svg, .fdcp-badge .fdcp-badge-start img,
.fdcp-badge .fdcp-badge-end i,
.fdcp-badge .fdcp-badge-end svg,
.fdcp-badge .fdcp-badge-end img {
  width: 1rem;
  height: 1rem;
}
.fdcp-badge .fdcp-badge-start button,
.fdcp-badge .fdcp-badge-end button {
  all: unset;
  cursor: pointer;
  border-radius: 50%;
  padding: 0.25rem;
  line-height: 0;
}
.fdcp-badge .fdcp-badge-content {
  display: inline-block;
}
.fdcp-badge.fdcp-badge-primary {
  background-color: #333333;
  color: #fff;
  border: 1px solid #333333;
}
.fdcp-badge.fdcp-badge-primary.inverted {
  background-color: #ffffff;
  color: #333333;
  border-color: #333333;
}
.fdcp-badge.fdcp-badge-secondary {
  background-color: #43474E;
  color: #fff;
  border: 1px solid #43474E;
}
.fdcp-badge.fdcp-badge-secondary.inverted {
  background-color: #ffffff;
  color: #43474E;
  border-color: #43474E;
}
.fdcp-badge.fdcp-badge-success {
  background-color: #03662a;
  color: #fff;
  border: 1px solid #03662a;
}
.fdcp-badge.fdcp-badge-success.inverted {
  background-color: #ffffff;
  color: #03662a;
  border-color: #03662a;
}
.fdcp-badge.fdcp-badge-danger {
  background-color: #d3080c;
  color: #fff;
  border: 1px solid #d3080c;
}
.fdcp-badge.fdcp-badge-danger.inverted {
  background-color: #ffffff;
  color: #d3080c;
  border-color: #d3080c;
}
.fdcp-badge.fdcp-badge-warning {
  background-color: #b3800f;
  color: #000;
  border: 1px solid #b3800f;
}
.fdcp-badge.fdcp-badge-warning.inverted {
  background-color: #ffffff;
  color: #b3800f;
  border-color: #b3800f;
}
.fdcp-badge.fdcp-badge-info {
  background-color: #1e7b96;
  color: #fff;
  border: 1px solid #1e7b96;
}
.fdcp-badge.fdcp-badge-info.inverted {
  background-color: #ffffff;
  color: #1e7b96;
  border-color: #1e7b96;
}
.fdcp-badge.fdcp-badge-light {
  background-color: #545961;
  color: #ffffff;
  border: 1px solid #545961;
}
.fdcp-badge.fdcp-badge-light.inverted {
  background-color: #ffffff;
  color: #545961;
  border-color: #545961;
}
.fdcp-badge.fdcp-badge-dark {
  background-color: #333333;
  color: #fff;
  border: 1px solid #333333;
}
.fdcp-badge.fdcp-badge-dark.inverted {
  background-color: #ffffff;
  color: #333333;
  border-color: #333333;
}

.fdcp-page-header-container {
  width: 100vw;
  position: relative;
  display: flex;
  top: 0;
  left: 50%;
  margin-left: -50vw;
  margin-top: -114px;
  padding-left: 0;
  padding-right: 0;
  height: 32rem;
  align-items: center;
  z-index: 0;
}
.fdcp-page-header-container.fdcp-page-header--has-bg {
  background: lightgray 0px -234px/100% 238.769% no-repeat;
}
.fdcp-page-header-container .text-container {
  max-width: 70%;
  max-height: 16rem;
  padding: 3rem 1rem 2rem calc((100vw - var(--gcds-container-size-xl)) / 2);
  border-radius: 0rem 3.125rem 3.125rem 0rem;
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.25), 34px -34px 34px 0px rgba(165, 165, 165, 0.1) inset, -34px 34px 34px 0px rgba(255, 255, 255, 0.1) inset;
  backdrop-filter: blur(34px);
}

.full-width-section {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  /* Remove max-width and padding if inherited from container */
  max-width: none;
  padding-left: 0;
  padding-right: 0;
}

body {
  overflow-x: hidden;
}

.img-responsive {
  width: 100%;
  height: auto;
}

.gc-form-hidden {
  display: none !important;
}